"use client";

import { useRef } from "react";
import { motion, useInView, useScroll, useTransform } from "framer-motion";
import Image from "next/image";
import ImageSlideshow from "@/components/ImageSlideshow";
import MotionImage from "@/components/MotionImage";
import { scrollToSection } from "@/utils/smoothScroll";
import LearnMoreLink from "@/components/LearnMoreLink";
import EventsTable from "@/components/EventsTable";
import { Event } from "@/components/EventsTable";

// Animation Variants
const fadeInUp = {
  hidden: { opacity: 0, y: 30 },
  visible: (index: number) => ({
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.6,
      delay: index * 0.15,
      ease: [0.22, 1, 0.36, 1],
    },
  }),
};

const fadeIn = {
  hidden: { opacity: 0 },
  visible: (delay: number) => ({
    opacity: 1,
    transition: {
      duration: 0.8,
      delay: delay,
      ease: [0.22, 1, 0.36, 1],
    },
  }),
};

const textReveal = {
  hidden: { opacity: 0, y: 100 },
  visible: (index: number) => ({
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.7,
      delay: 0.1 * index,
      ease: [0.22, 1, 0.36, 1],
    },
  }),
};

// Image List for Slideshow
const images = [
  "https://picsum.photos/300/400",
  "https://picsum.photos/200/400",
  "https://picsum.photos/600/400",
];

export default function Home() {
  const biographyRef = useRef<HTMLDivElement>(null);
  const heroRef = useRef(null);
  const bioImageRef = useRef(null);

  const isHeroInView = useInView(heroRef, { once: false, amount: 0.3 });
  const isBioImageInView = useInView(bioImageRef, { once: false, amount: 0.3 });

  // Scroll effect for Bio Image
  const { scrollYProgress: bioImageScroll } = useScroll({
    target: bioImageRef,
    offset: ["start end", "end start"],
  });

  const bioImageScale = useTransform(bioImageScroll, [0, 0.5, 1], [0.95, 1, 0.98]);

  return (
    <>
      {/* Hero Section */}
      <motion.section
        ref={heroRef}
        id="hero"
        className="h-screen w-full flex flex-col items-center justify-center text-white relative"
        initial="hidden"
        animate={isHeroInView ? "visible" : "hidden"}
        variants={fadeIn}
        custom={0}
      >
        <div className="text-center px-4">
          <motion.div variants={fadeInUp} custom={0} className="flex justify-center">
            <Image
              src="/logo_2025.svg"
              alt="Brand Logo"
              width={200}
              height={200}
              className="w-72 h-auto"
              priority
            />
          </motion.div>

          <motion.div variants={fadeInUp} custom={1} className="flex flex-col items-center">
            <h2 className="text-lg md:text-xl font-bold tracking-[0.3em] uppercase">Session Drummer</h2>
            <p className="text-xs font-bold tracking-[0.3em] uppercase">London</p>
          </motion.div>
        </div>

        {/* Scroll Indicator */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 1, duration: 0.8 }}
          className="absolute bottom-10 cursor-pointer text-gray-400 text-xs tracking-widest"
          onClick={() => scrollToSection("biography")}
        >
          Scroll To Explore
          <motion.div animate={{ y: [0, 10, 0] }} transition={{ repeat: Infinity, duration: 1.5 }}>
            â†“
          </motion.div>
        </motion.div>
      </motion.section>

      {/* Biography Section */}
      <motion.section
        id="biography"
        ref={biographyRef}
        className="w-full lg:h-[90vh] bg-black text-white"
        initial="hidden"
        animate="visible"
        variants={fadeIn}
        custom={0.2}
      >
        <div className="flex flex-col lg:flex-row h-full">
          {/* Image */}
          <motion.div
            ref={bioImageRef}
            className="w-full h-96 lg:h-full lg:w-2/3 flex items-end"
            initial="hidden"
            animate={isBioImageInView ? "visible" : "hidden"}
            variants={fadeIn}
            custom={0.3}
          >
            <MotionImage
              src="/images/gallery/profile_hoxton.JPG"
              alt="Ollie Woods at Hoxton"
              className="w-full h-auto"
              style={{ scale: bioImageScale }}
            />
          </motion.div>

          {/* Bio Content */}
          <div className="h-full w-full lg:w-1/3 bg-custom-main text-white px-4 lg:pt-16 xl:pt-24 flex flex-col justify-between">
            <motion.h2 variants={textReveal} custom={0} className="text-6xl font-bold mb-16">
              BIO.
            </motion.h2>

            <motion.div className="max-w-2xl text-sm leading-6 space-y-6">
              <motion.p variants={textReveal} custom={1} className="indent-8">
                Ollie Woods is an <span className="font-bold">in-demand session drummer</span> working
                with internationally acclaimed artists. He has performed with{" "}
                <span className="font-bold">Cynthia Erivo</span> at the London Palladium, toured Asia
                with <span className="font-bold">Samantha Barks</span>, and worked with{" "}
                <span className="font-bold">BBC Radio 1</span>.
              </motion.p>
              <motion.p variants={textReveal} custom={2} className="indent-8">
                He has developed a <span className="font-bold">library of transcriptions</span> for
                drummers on Instagram, reaching over <span className="font-bold">100,000</span> people.
                Ollie welcomes new musical ventures and loves collaborating with exciting artists.
              </motion.p>
            </motion.div>

            <motion.div className="text-right w-full mb-4" variants={fadeInUp} custom={3}>
              <LearnMoreLink href="/about" />
            </motion.div>
          </div>
        </div>
      </motion.section>

      {/* Image Slideshow */}
      <motion.section id="landingCarousel" className="w-full overflow-hidden" variants={fadeIn}>
        <ImageSlideshow slides={images} />
      </motion.section>

      {/* Selected Works */}
      <motion.div initial="hidden" animate="visible" variants={fadeInUp} custom={4}>
        <EventsTable title="Selected Works." />
      </motion.div>
    </>
  );
}
